import React from 'react'

const user = {
    _id: 'sfvbndfjkv',
    role: 'admin'
}

const Header = () => {
    return (
        <div className='header'>
          <Link href={'/'} onClick={() => setIsOpen(false)}>Home</Link>
          <Link href={'/search'} onClick={() => setIsOpen(false)}><FaSearch /></Link>
          <Link href={'/cart'} onClick={() => setIsOpen(false)}><FaShoppingBag /></Link>
          {user?._id ? (
            <>
              <button onClick={() => setIsOpen(!isOpen)}><FaUser /></button>
              <dialog open={isOpen}>
                <div>
                  {user?.role === 'admin' && <Link href={'/admin/dashboard'} onClick={() => setIsOpen(false)}>Admin</Link>}
                  <Link href={'/order'} onClick={() => setIsOpen(false)}>Order</Link>
                  <Link href={'/profile'} onClick={() => setIsOpen(false)}>Profile</Link>
                  <button onClick={handleLogout}><FaSignOutAlt /></button>
                </div>
              </dialog>
            </>
          ) : (
            <Link href={'/login'}><FaSignInAlt /></Link>
          )}
        </div>
      )
}

export default Header;